import React, { useState, useEffect } from 'react'
import { showNotification } from './NoticationBar'
import { setLoadScreen } from './LoadDialog'
import { GetLogs, CheckIfPostExists, RemoveLog } from '../Communicator'
import { showPostPage } from './PostPage'
import { Page } from '../Config'

let _setLogs

function ModeratorPage({setDivision}) {
    return (
        <div id="moderator-page">
            <p className="title">MODERATION PANEL</p>
            <div className="divider"></div>
            <ModerationLog setDivision={setDivision}></ModerationLog>
        </div>
    )
}

function ModerationLog({setDivision}) {
    const [logs, setLogs] = useState([])
    _setLogs = setLogs

    return (
        <Page.Consumer>
            {page => <div className="mod-log">
                <p className="title">RECENT REPORTS</p>
                <p className="description">Once a reported post has been deleted, remove it using the 'check' button on each log.</p> 
                <div className="divider"></div>
                <div className="log-holder">
                    {
                        logs.length > 0 ? logs.map(log => <Log key={log._id} log={log} setLogs={setLogs} page={page} setDivision={setDivision}></Log>) : <p className="empty">NO LOGS FOUND</p>
                    }
                </div>
                <div className="paging">
                    <div className="next selector">NEXT</div>
                    <div className="previous selector">PREVIOUS</div>
                </div>
            </div>
            }
        </Page.Consumer>
    )
}

function Log({ log, setLogs, page, setDivision }) {
    return (
        <div className="log-tile" onClick={() => { showPost(log, log.time, setDivision, page) }}>
            <div className="main">
                <p className="log-title">{log.postTitle}</p>
                <p>Made by: <span>{log.postUsername}</span></p>
            </div>
            <i className="icon fas fa-check" onClick={async (e) => {
                e.stopPropagation()
                let result = await RemoveLog(log._id)
                if(result.status == 'success')
                    setLogs(state => state.filter(l => l._id != log._id))
            }}></i>
            <p className="flags">{log.totalFlags}</p>
        </div>
    )
}

export async function loadLogs() {
    setLoadScreen(true)
    let result = await GetLogs(0)
    if (result.status == 'failure')
        _setLogs([])
    else
        _setLogs(result.data)
    setLoadScreen(false)
}

async function showPost(log, time, setDivision, page) {
    setLoadScreen(true)
    let result = await CheckIfPostExists(log.postId)
    setLoadScreen(false)

    if (result.status == 'failure')
        return showNotification("Post does not exist")

    let post = result.data

    let votes = post.votes
    let totalVotes = 0
    let timeVal = parseInt(time)
    let date = new Date(timeVal)

    for (let i = votes.length - 1; i >= 0; i--) {
        totalVotes += votes[i].votes

        if (votes[i].tag === page.currentTags)
            break
    }

    let newPost = post
    newPost.totalVotes = totalVotes
    newPost.date = date.toLocaleString()
    setDivision('post')
    showPostPage(true, newPost)
}

export default ModeratorPage