import React, {useState} from 'react'

let message = ''
let changeNotificationVisibility  = null

export function NotificationBar(){

    const [visible, setVisibility] = useState(false)
    changeNotificationVisibility = setVisibility

    if(!visible)
        return null

    return(
        <div id="notification-bar">
            <p className="description">{message}</p>
        </div>
    )
}

export function showNotification(mssg, callback){
    message = mssg
    if(changeNotificationVisibility == null)
        return

    changeNotificationVisibility(true)
    callback && callback()

    setTimeout(() => {
        message = ""
        changeNotificationVisibility(false)        
    }, 4000)
}