function renderTree(commentArray, commentDictionary) {

    function transformToObject(item, parentId) {
        return {
            _id: item._id,
            isLeaf: item.isLeaf,
            username: item.username,
            description: item.description,
            parentId: parentId,
            votes: item.votes
        }
    }

    function search(childrenIds, parentId) { //breadth first
        let childrenTree = []
        let rowStack = []
        let firstRow = []

        for (let childId of childrenIds) {
            firstRow.push(transformToObject(commentDictionary[childId], parentId))

            for (let id of commentDictionary[childId].childrenIds) {
                rowStack.push({ id: id, parentId: childId })
            }
        }

        childrenTree.push(firstRow)

        while (rowStack.length != 0) {
            let row = []
            let newChildRow = []

            for (let child of rowStack) {
                row.push(transformToObject(commentDictionary[child.id], child.parentId))

                for (let id of commentDictionary[child.id].childrenIds) {
                    newChildRow.push({ id: id, parentId: child.id })
                }
            }

            rowStack = newChildRow
            childrenTree.push(row)
        }

        return childrenTree
    }

    let tree = []

    for (let item of commentArray) {
        if (item.isChild)
            continue

        let branch = search(item.childrenIds, item._id) //is tree

        if (branch[0].length == 0) {
            tree.push([[item]])
        } else {
            branch.unshift([item])
            tree.push(branch)
        }

    }
    return tree
}

