function Comment({ childrenTree, level, parentId, username, description, isLeaf, votes }) {
    const [replySelected, setReplySelected] = useState(false)
    const [reply, setReply] = useState('')
    const [vote, setVotes] = useState(votes)
    const tArea = useRef(null)

    return (
        <div className="comment">
            <div className="main-section">
                <div className="top">
                    <p className="comment-user">{username}</p>
                    <p className="comment-description">{description}</p>

                    <div className="votes">
                        <div className="column">
                            <i onClick={() => { voteComment('add', parentId, () => {setVotes(state => state + 1)}) }} className="upvote fas fa-caret-up"></i>
                            <p className="votes">{vote}</p>
                            <i onClick={() => { voteComment('subtract', parentId, () => {setVotes(state => state - 1)}) }} className="downvote fas fa-caret-down"></i>
                        </div>
                    </div>
                </div>
                <User.Consumer>{
                    user => user.username == null ? <></> :
                        <div className="reply-section">
                            {
                                !replySelected ?
                                    <div className="reply-lower">
                                        <p onClick={() => { setReplySelected(true) }}>REPLY</p>
                                        <CommentDropdown username={username} user={user}></CommentDropdown>
                                    </div> :
                                    <div>
                                        <textarea ref={tArea} onChange={(e) => { setReply(e.target.value) }}></textarea>
                                        <div className="reply-lower">
                                            <p onClick={async () => {
                                                let result = await MakeComment(reply, true, parentId)
                                                tArea.current.value = ''
                                            }}>SEND</p>
                                            <CommentDropdown username={username} user={user}></CommentDropdown>
                                        </div>
                                    </div>
                            }
                        </div>
                }
                </User.Consumer>
            </div>

            <div className={!isLeaf ? "children" : ""}>
                {
                    level >= childrenTree.length ? <></> : childrenTree[level].filter(child => child.parentId == parentId).map(child => <Comment key={child._id} isLeaf={child.isLeaf} username={child.username} parentId={child._id} description={child.description} votes={child.votes} level={level + 1} childrenTree={childrenTree}></Comment>)
                }
            </div>
        </div>
    )
}

function CommentInput() {
    const [input, setInput] = useState('')
    const textInput = <textarea placeholder="Write your comment here..." onChange={(e) => { setInput(e.target.value) }}></textarea>

    return [input, textInput]
}

function FilterBar(props) {
    let setCommentFilter = props.setCommentFilter

    const [dropdownVisibility, setDropdownVisibility] = useState(false)
    const [buttonDisplayData, setButtonDisplayData] = useState(['TOP', 'fas fa-chart-line'])
    const dropdown = useRef(null)

    function dropdownSelect(filter, icon) {
        setCommentFilter(filter.toUpperCase())
        setButtonDisplayData([filter.toUpperCase(), icon])
        setDropdownVisibility(false)
    }

    function documentClick(e) {
        if (dropdown.current && !dropdown.current.contains(e.target) && dropdownVisibility) {
            setDropdownVisibility(false)
        }
    }

    useEffect(() => {
        document.addEventListener('mousedown', documentClick)

        return () => {
            document.removeEventListener('mousedown', documentClick)
        }
    }, [dropdownVisibility])

    return (
        <div id="filterbar">
            <div id="filter-selector" ref={dropdown} onClick={() => { setDropdownVisibility(state => !state) }}>
                <i className={buttonDisplayData[1]}></i>
                <div className="divider"></div>
                <p>{buttonDisplayData[0]}</p>
                {
                    !dropdownVisibility ? <></> : <div className="dropdown" style={{ right: '0px', left: 'auto' }} onClick={(e) => { e.stopPropagation() }}>
                        <p onClick={() => { dropdownSelect('Top', 'fas fa-chart-line') }}>Top</p>
                        {/* <p onClick={() => { dropdownSelect('Hot', 'fas fa-burn') }}>Hot</p> */}
                        {/* <p onClick={() => { dropdownSelect('Trending', 'fab fa-red-river') }}>Trending</p> */}
                        <p onClick={() => { dropdownSelect('New', 'fas fa-certificate') }}>New</p>
                        <p onClick={() => { dropdownSelect('Controvertial', 'fas fa-skull-crossbones') }}>Controvertial</p>
                    </div>
                }
            </div>
        </div>
    )
}

