function voteComment(voteType, id, callback) {
    DoVoteComment(voteType, id)
        .then(res => {
            if(res.status == 'success')
                callback(voteType)
        })
}

export async function showPostPage(visibility, post) {
    if (!visibility)
        return changeVisibility(false)

    //show loading
    let result = await GetPostComments(post._id)

    let commentArray = Object.values(result.data)
    post.commentArray = commentArray
    post.commentDictionary = result.data

    currentPost = post
    changeVisibility(true)
}