import React, { useState, useEffect } from 'react'
import { Login, SubmitPost, MuteUser } from '../Communicator'
import { showNotification } from './NoticationBar'
import { User, Page } from '../Config'
import { isEmptyOrSpaces, validURL } from '../Utils'
import { Dropdown } from './Dropdown'
import FitText from 'react-fittext'

let modalSpecList = []

function ModalSpec(modalName, modalFunc, additionalInfo) {
    this.modalName = modalName
    this.modalFunc = modalFunc
    this.Run = (visibility) => { this.modalFunc(visibility) }
    this.additionalInfo = additionalInfo
}

export function LoginModal() {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [user, setUser] = useState(null)
    const [visible, setVisibility] = useState(false)

    useEffect(() => {
        modalSpecList.push(new ModalSpec("login", setVisibility))
    }, [])

    if (!visible)
        return null

    return (
        <div onClick={() => { setModalVisibility("login", false) }} id="modal">
            <div className="login" id="dialog" onClick={(e) => { e.stopPropagation() }}>
                <p className="title">LOG-IN</p>
                <p>Username:</p>
                <input className="username" onChange={(event) => { setUsername(event.target.value) }} type="text"></input>
                <p>Password:</p>
                <input className="password" onChange={(event) => { setPassword(event.target.value) }} type="password"></input>
                <div className="space"></div>
                <User.Consumer>{
                    user => <div className="btn default" onClick={async () => {

                        let data = await Login(username, password)

                        if (data.status === "success") {
                            setModalVisibility("login", false)
                            let now = new Date()
                            let time = now.getTime()
                            time += 3600000
                            now.setTime(time)
                            document.cookie = `token=${data.data.token}; expires=${now.toUTCString()};`
                            user.editUser({
                                username: username,
                                token: data.data.token,
                                followingSports: data.data.followedSports,
                                followingCompetitions: data.data.followedCompetitions,
                                followingTeams: data.data.followedTeams,
                                followers: data.data.followers,
                                following: data.data.following,
                                rank: data.data.rank,
                                moderatedTags: data.data.moderatedTags
                            })
                        }


                        showNotification(data.message)
                    }}>
                        LOGIN
                </div>
                }</User.Consumer>
            </div>
        </div>
    )
}

export function FeedbackModal() {
    const [text, setText] = useState('')
    const [topic, setTopic] = useState(null)
    const [visible, setVisibility] = useState(false)

    useEffect(() => {
        modalSpecList.push(new ModalSpec("feedback", setVisibility))
    }, [])

    if (!visible)
        return null

    return (
        <div onClick={(e) => { setModalVisibility("feedback", false) }} id="modal">
            <div className="login" id="dialog" onClick={(e) => { e.stopPropagation() }}>
                <p className="title">FEEDBACK</p>
                <p>SELECT QUESTION TYPE:</p>
                <div style={{ margin: "10px 0px" }}>
                    <Dropdown title="Select..." expanded={'200px'} choices={[
                        { name: 'FEATURE SUGGESTION', action: (title) => { title('FEATURE SUGGESTION'); setTopic('feature_suggestion') } },
                        { name: 'SITE PROBLEM', action: (title) => { title('SITE PROBLEM'); setTopic('site_problem') } },
                        { name: 'QUESTION', action: (title) => { title('QUESTION'); setTopic('question') } },
                    ]}></Dropdown>
                </div>
                <p>MESSAGE:</p>
                <input className="username" onChange={(event) => { setText(event.target.value) }} type="text"></input>
                <div className="space"></div>
                <div className="btn default" onClick={async () => {
                    if (topic == null)
                        return showNotification('Please select topic')
                    //Send message
                    showNotification('Message sent')
                }}>
                    SEND
                </div>
            </div>
        </div>
    )
}

export function CreatePostModal() { //Hide main page when shown, or?

    const [visible, setVisibility] = useState(false)
    const [title, setTitle] = useState('')
    const [body, setBody] = useState('') //SET VALIDATION
    const [link, setLink] = useState('') //SET VALIDATION
    const [selectedTab, changeSelectedTab] = useState(2)

    useEffect(() => {
        modalSpecList.push(new ModalSpec("createpost", setVisibility))
    }, [])

    if (!visible)
        return null

    return (
        <div id="create-post-dialog">
            <div className="create-post-container">
                <h2 className="title">CREATE A POST</h2>
                <div className="divider"></div>
                <div className="content-holder">
                    <div className="type-selector">
                        {/* <div className={`type ${selectedTab == 1 ? 'selected' : ''}`} onClick={() => { changeSelectedTab(1) }}>Text</div> */}
                        {/* <div className={`type ${selectedTab == 2 ? 'selected' : ''}`} onClick={() => { changeSelectedTab(2) }}>Embed</div> */}
                    </div>
                    <p className="disclaimer">
                        Please enter a valid media URL from Facebook, Instagram, Youtube or any of the supported websites.
                </p>
                    <input placeholder="Enter title..." onChange={(e) => { setTitle(e.target.value) }}></input>
                    {
                        selectedTab == 1 ? <textarea placeholder="Enter description..." onChange={(e) => { setBody(e.target.value) }}></textarea> :
                            <input className="link" placeholder="Enter link..." onChange={(e) => { setLink(e.target.value) }}></input>
                    }
                    <div className="row">
                        <Page.Consumer>
                            {page =>
                                <div className="button fixed" onClick={() => { submitPost(title, body, page.tags, selectedTab == 2 ? true : false, link) }}>
                                    <div className="items">
                                        <p className="text">POST</p>
                                    </div>
                                    <div className="shadow"></div>
                                </div>
                            }
                        </Page.Consumer>
                        <div className="button fixed" onClick={() => { setVisibility(false) }}>
                            <div className="items">
                                <p className="text">CANCEL</p>
                            </div>
                            <div className="shadow"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export function MuteUserModal() {
    const [visible, setVisibility] = useState(false)
    const [hours, setHours] = useState(0)
    const [minutes, setMinutes] = useState(0)

    useEffect(() => {
        modalSpecList.push(new ModalSpec("muteuser", setVisibility))
    }, [])

    if (!visible)
        return null

    function convertTime(hours, minutes) {
        return (hours * 3600000) + (minutes * 60000)
    }

    return (
        <div onClick={() => { setModalVisibility("muteuser", false) }} id="modal">
            <div id="mute-user-modal">
                <div id="modal">
                    <div id="dialog" onClick={e => e.stopPropagation()}>
                        <p>Please enter the duration for which you would like to mute this user:</p>
                        <div style={{ marginTop: '10px' }}>
                            <p>Hours:</p>
                            <input onChange={(e) => { setHours(parseInt(e.target.value)) }} defaultValue={0} type="number"></input>
                            <p>Minutes:</p>
                            <input onChange={(e) => { setMinutes(parseInt(e.target.value)) }} defaultValue={0} type="number"></input>
                        </div>
                        <div style={{ marginTop: '15px' }} className="btn default" onClick={async () => {
                            if (isNaN(hours) || isNaN(minutes))
                                return

                            MuteUser(modalSpecList.find(item => item.modalName == "muteuser").additionalInfo, convertTime())
                            setModalVisibility("muteuser", false)
                        }}>MUTE</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export function SingleTextModal() { //Passes with the same name to the list multiple times, could cause issues
    const [visible, setVisibility] = useState(false)
    const [text, setText] = useState('');

    useEffect(() => {
        modalSpecList.push(new ModalSpec("singletext", setVisibility))
    }, [])

    if (!visible)
        return null

    return (
        <div onClick={() => { setModalVisibility("singletext", false) }} id="modal">
            <div id="mute-user-modal">
                <div id="modal">
                    <div id="dialog" onClick={e => e.stopPropagation()}>
                        <p>{modalSpecList.find(i => i.modalName == "singletext").additionalInfo.title}</p>
                        <div style={{ marginTop: '10px' }}>
                            <input onChange={(e) => { setText(e.target.value) }} style={{ width: '100%' }}></input>
                        </div>
                        <div style={{ marginTop: '15px' }} className="btn default" onClick={async () => {
                            modalSpecList.find(i => i.modalName == "singletext").additionalInfo.callback(text)
                            setModalVisibility("singletext", false)
                        }}>{modalSpecList.find(i => i.modalName == "singletext").additionalInfo.buttonText}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

