//
async function submitPost(title, body, tags, isEmbed, link) {

    if (!checkLink(link)) {
        showNotification('Enter a valid address')
        return
    }

    let data = await SubmitPost(title, body, tags, isEmbed, link, Date.now())

    if (data.status === "success")
        showNotification('Posted successfully')

    setModalVisibility("createpost", false)
}

function checkLink(link) {
    if (isEmptyOrSpaces(link))
        return false

    if (!link.includes('facebook') && !link.includes('instagram') && !link.includes('twitter') && !link.includes('youtube'))
        return false

    if (!validURL(link))
        return false

    return true
}

export function setModalVisibility(name, visibility, additionalInfo = {}) {
    let spec = modalSpecList.find(item => item.modalName == name)
    spec.additionalInfo = additionalInfo
    spec.Run(visibility)
}
