import React, { useState } from 'react'
import { GetLeagues, GetTeams, Register } from '../Communicator'
import { setLoadScreen } from './LoadDialog'
import { showNotification } from './NoticationBar'
import Recaptcha from 'react-google-recaptcha'

let selectedItems = {
    sports: new Set(),
    competitions: new Set(),
    teams: new Set()
}

let username = ''
let password = ''
let confirmPass = ''

let changeVisibility = null

export function RegisterPage() {

    const [visible, setVisibility] = useState(false)
    const [page, setPage] = useState(0)
    const [leagues, setLeagues] = useState([])
    const [teams, setTeams] = useState([])

    changeVisibility = setVisibility

    if (!visible) {
        selectedItems = {
            sports: new Set(),
            competitions: new Set(),
            teams: new Set()
        }

        username = ''
        password = ''
        confirmPass = ''

        return null
    }

    async function sportsPageOnClick() {
        setLoadScreen(true)
        let leagues = await GetLeagues()
        setLeagues(leagues.data.leagues.map(league => league.name))
        setLoadScreen(false)

        setPage(2)
    }

    async function competitionPageOnClick() {
        setLoadScreen(true)
        let teams = await GetTeams()
        let set = new Set(teams.data.teams.map(team => team.name))
        setTeams(Array.from(set))
        setLoadScreen(false)

        setPage(3)
    }

    async function teamPageOnClick() {
        let sports = Array.from(selectedItems.sports)
        let competitions = Array.from(selectedItems.competitions)
        let teams = Array.from(selectedItems.teams)

        teams = teams.map(item => [`${item}_team_result`, item])
        competitions = competitions.map(item => [`${item}_competition_result`, item])

        let itemsToArray = {
            sports: sports,
            competitions: competitions,
            teams: teams
        }

        let result = await Register(username, password, itemsToArray)

        if (result.status == "success") {
            showNotification('Account created')
            setVisibility(false)
        } else {
            showNotification(result.message)
            setVisibility(false)
        }

        setPage(0)
    }

    return (
        <div id="register-page-holder">
            <div id="register-page">
                <div className="header">
                    <i className="exit fas fa-times-circle" onClick={() => { setVisibility(false); setPage(0) }}></i>
                    <p>REGISTER</p>
                </div>
                <div className="body">
                    <InitialPage setPage={setPage} enabled={page == 0 ? true : false}></InitialPage>
                    <Page items={['Soccer']} clicked={sportsPageOnClick} setPage={setPage} type={'sports'} enabled={page == 1 ? true : false}></Page>
                    <Page items={leagues} clicked={competitionPageOnClick} setPage={setPage} type={'competitions'} enabled={page == 2 ? true : false}></Page>
                    <Page items={teams} clicked={teamPageOnClick} setPage={setPage} type={'teams'} enabled={page == 3 ? true : false}></Page>
                </div>
            </div>
        </div>
    )
}

function InitialPage(props) {

    const [captchaPassed, setCaptchaPassed] = useState(false)

    if (!props.enabled)
        return null

    async function click() {
        if (isEmptyOrSpaces(username)) {
            showNotification('Please enter username')
            return
        }

        if (isEmptyOrSpaces(password) || isEmptyOrSpaces(confirmPass)) {
            showNotification('Please enter password')
            return
        }

        if (password != confirmPass) {
            showNotification('Passwords do not match')
            return
        }

        if (password.length < 3) {
            showNotification('Password length must be more than 3 letters')
            return
        }

        props.setPage(1)
    }

    return (
        <div id="register-initial">
            <p className="">Username:</p>
            <input onChange={(e) => { username = e.target.value }}></input>
            <p className="">Password:</p>
            <input onChange={(e) => { password = e.target.value }} type="password"></input>
            <p className="">Retype password:</p>
            <input onChange={(e) => { confirmPass = e.target.value }} type="password"></input>
            <div className="space"></div>
            <Recaptcha sitekey="6LdpI8QZAAAAAP0RaC5gvSJTzpL-35bSqSQZVMGk" onChange={() => { setCaptchaPassed(true) }}>

            </Recaptcha>
            {
                !captchaPassed ? <></> : <div className="button gap" onClick={() => { click() }}>
                    <div className="items">
                        <div className="leading">
                            <p className="text">NEXT</p>
                        </div>
                        <i className="trailing fas fa-arrow-right"></i>
                    </div>
                    <div className="shadow"></div>
                </div>
            }
        </div>
    )
}

function Page(props) {

    if (!props.enabled)
        return null

    return (
        <div id="register-sports">
            <p>Select your favorite {props.type}:</p>
            <div className="panel">
                <div className="items">
                    <TileHolder type={props.type} items={props.items}></TileHolder>
                </div>
            </div>
            <div className="button gap" onClick={() => { props.clicked() }}>
                <div className="items">
                    <div className="leading">
                        <p className="text">NEXT</p>
                    </div>
                    <i className="trailing fas fa-arrow-right"></i>
                </div>
                <div className="shadow"></div>
            </div>
        </div>
    )
}

function TileHolder(props) {
    return (
        props.items.map(item => <Tile type={props.type} key={item} title={item}></Tile>)
    )
}

function Tile(props) {

    const [style, setStyle] = useState('')

    function onClick() {
        style == '' ? setStyle('invert') : setStyle('')
        style == '' ? selectedItems[props.type].add(props.title) : selectedItems[props.type].delete(props.title)
    }

    return (
        <div className={`tile ${style}`} onClick={() => { onClick() }}>
            <div className={`dot ${style}`}></div>
            {/* <div className="divider"></div> */}
            <p className="name">{props.title}</p>
        </div>
    )
}

export function enablePage() {
    changeVisibility(true)
}

function isEmptyOrSpaces(str) {
    return str === null || str.match(/^ *$/) !== null;
}