import React, { useState, useEffect } from 'react'
import background from '../media/content-background.jpg'
import { GetFixtureDetails } from '../Communicator'

let changeVisibility
let currentMatch

function ContentPage(props) {

    //const [visible, setVisibility] = useState(false)
    const [fixtureDetails, setFixtureDetails] = useState({})
    //changeVisibility = setVisibility

    useEffect(() => {
        if (!props.isActivated)
            return

        async function GetDetails() {
            let res = await GetFixtureDetails(currentMatch.fixture_id)
            console.log(res.data)
            setFixtureDetails(res.data)
        }

        GetDetails()
    }, [props.isActivated])

    if (!props.isActivated)
        return null

    let date = new Date(Date.parse(currentMatch.event_date))

    return (
        <div id="content-page">
            <div id="content-header">
                <img src={background} alt='back' />
                <div className="filter"></div>
                <div className="header-content">
                    {/* <i onClick={() => { }} className="back fas fa-arrow-circle-left"></i> */}
                    <p className="date">{date.toLocaleDateString()}</p>
                    <p className="league">{currentMatch.league.name}</p>
                    <div className="gap"></div>
                    <div className="score-front">
                        <p className="team">{currentMatch.homeTeam.team_name}</p>
                        <div className="score-display">
                            <p className="num">{currentMatch.goalsHomeTeam}</p>
                        </div>
                        <div className="score-display">
                            <p className="num">{currentMatch.goalsAwayTeam}</p>
                        </div>
                        <p className="team">{currentMatch.awayTeam.team_name}</p>
                    </div>
                    <p className="location">{currentMatch.venue}</p>
                    <p className="ht-score">{`HT Score: ${currentMatch.score == null || currentMatch.score.halftime == null ? '-' : currentMatch.score.halftime}`}</p>
                </div>

            </div>
            <div id="content-body">
                <MatchAttribute title="SCORE" team1={currentMatch.homeTeam.team_name} value1={currentMatch.goalsHomeTeam} team2={currentMatch.awayTeam.team_name} value2={currentMatch.goalsAwayTeam}></MatchAttribute>
                <MatchAttribute title="POSSESSION" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Ball Possession"] === undefined ? '-' : fixtureDetails["Ball Possession"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Ball Possession"] === undefined ? '-' : fixtureDetails["Ball Possession"].away}></MatchAttribute>
                <MatchAttribute title="SHOTS ON TARGET" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Shots on Goal"] === undefined ? '-' : fixtureDetails["Shots on Goal"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Shots on Goal"] === undefined ? '-' : fixtureDetails["Shots on Goal"].away}></MatchAttribute>
                <MatchAttribute title="SHOTS OFF TARGET" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Shots off Goal"] === undefined ? '-' : fixtureDetails["Shots off Goal"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Shots off Goal"] === undefined ? '-' : fixtureDetails["Shots off Goal"].away}></MatchAttribute>
                <MatchAttribute title="CORNERS" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Corner Kicks"] === undefined ? '-' : fixtureDetails["Corner Kicks"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Corner Kicks"] === undefined ? '-' : fixtureDetails["Corner Kicks"].away}></MatchAttribute>
                <MatchAttribute title="OFFSIDES" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Offsides"] === undefined ? '-' : fixtureDetails["Offsides"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Offsides"] === undefined ? '-' : fixtureDetails["Offsides"].away}></MatchAttribute>
                <MatchAttribute title="FOULS" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Fouls"] === undefined ? '-' : fixtureDetails["Fouls"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Fouls"] === undefined ? '-' : fixtureDetails["Fouls"].away}></MatchAttribute>
                <MatchAttribute title="RED CARDS" team1={currentMatch.homeTeam.team_name} value1={fixtureDetails["Red Cards"] === undefined ? '-' : fixtureDetails["Red Cards"].home} team2={currentMatch.awayTeam.team_name} value2={fixtureDetails["Red Cards"] === undefined ? '-' : fixtureDetails["Red Cards"].away}></MatchAttribute>
            </div>
        </div>
    )
}

function MatchAttribute(props) {
    return (
        <div className="match-attribute">
            <p className="title">{props.title}</p>
            <div className="attribute">
                <div className="row">
                    <p className="name">{props.team1}</p>
                    <p className="score">{props.value1}</p>
                </div>
                <div className="divider"></div>
                <div className="row">
                    <p className="name">{props.team2}</p>
                    <p className="score">{props.value2}</p>
                </div>
            </div>
        </div>
    )
}

export function ShowContentPage(visibility, _currentMatch) { //remove visibility
    currentMatch = _currentMatch
}

export function GetCurrentMatch(){
    return currentMatch
}

export default ContentPage