import React, { useEffect, useState } from 'react'
import { setLoadScreen } from './LoadDialog'
import { GetPostsByUser, GetUserInfo, FollowUser, PromoteToMod, DemoteToUser, ChangeSocial } from '../Communicator'
import { User } from '../Config'
import { showNotification } from './NoticationBar'
import { setModalVisibility } from './Modal'

let changeVisibility = null
let changePosts = null
let changeUserInfo = null
let currentUser = null

export function Profile() { //Should not be used anymore
    const [visibility, setVisibility] = useState(false)
    const [posts, setPosts] = useState([])
    const [userInfo, setUserInfo] = useState({})

    useEffect(() => {
        changePosts = setPosts
        changeVisibility = setVisibility
        changeUserInfo = setUserInfo
    }, [])

    if (!visibility)
        return null

    return (
        <div className="user-profile">
            <div className="header">
                <i className="exit fas fa-times-circle" onClick={() => { setVisibility(false) }}></i>
                <p>{currentUser}</p>
            </div>
            <div className="stats">
                <div className="followers card">
                    <i className="icon fas fa-user-friends"></i>
                    <p className="title">Followers</p>
                    <p className="value">{userInfo.followers ? userInfo.followers.length : 0}</p>
                    <div className="shadow"></div>
                </div>
                <div className="likes card">
                    <i className="icon fas fa-thumbs-up"></i>
                    <p className="title">Likes</p>
                    <p className="value">{userInfo.totalVotes ? userInfo.totalVotes : 0}</p>
                    <div className="shadow"></div>
                </div>
                <div className="date card">
                    <i className="icon fas fa-calendar-alt"></i>
                    <p className="title">Joined</p>
                    <p className="value">{userInfo.dateJoined}</p>
                    <div className="shadow"></div>
                </div>
            </div>
            <div className="divider"></div>
            <div className="social">
                <SocialLink logo="fab fa-instagram" name="Instagram: "></SocialLink>
                <SocialLink logo="fab fa-youtube" name="Youtube: "></SocialLink>
                <SocialLink logo="fab fa-twitter" name="Twitter: "></SocialLink>
                <SocialLink logo="fab fa-facebook" name="Facebook: "></SocialLink>
                <SocialLink logo="fab fa-reddit" name="Reddit: "></SocialLink>
                <SocialLink logo="fab fa-pinterest" name="Pinterest: "></SocialLink>
            </div>
            <div className="divider"></div>
            <div className="follow-btn" onClick={async () => {
                let result = await FollowUser(currentUser)
                if (result.status == 'failure')
                    return

                let updatedInfo = userInfo
                updatedInfo.followers.push(currentUser) //should add this user
                setUserInfo({ ...updatedInfo })
            }}>
                <p>FOLLOW</p>
            </div>
            <div className="divider"></div>
            <p className="post-title">POSTS</p>
            {
                posts.map(post => <ProfilePost key={post._id} title={post.title} body={post.body} tags={post.tags} votes={post.votes[0].votes}></ProfilePost>)
            }
        </div>
    )
}

export function ProfileDesktop(props) {
    let username = props.username

    const [userInfo, setUserInfo] = useState({socialLinks: {}})

    useEffect(() => {
        (() => {
            setLoadScreen(true)
            let postsPromise = GetPostsByUser(currentUser)
            let userPromise = GetUserInfo(username)

            Promise.allSettled([postsPromise, userPromise])
                .then(results => {
                    let posts = results[0].value
                    let user = results[1].value

                    if (posts.status == 'failure' || user.status == 'failure')
                        return console.log('Failed')

                    //posts = post.data
                    setUserInfo(state => user.data)
                    setLoadScreen(false)
                })
        })()
    }, [])

    return (
        <div className="user-profile desktop">
            <div className="header">
                <p>{username}</p>
            </div>
            <div className="stats">
                <div className="followers card">
                    <i className="icon fas fa-user-friends"></i>
                    <p className="title">Followers</p>
                    <p className="value">{userInfo.followers ? userInfo.followers.length : 0}</p>
                    {/* <div className="shadow"></div> */}
                </div>
                <div className="likes card">
                    <i className="icon fas fa-thumbs-up"></i>
                    <p className="title">Likes</p>
                    <p className="value">{userInfo.totalVotes ? userInfo.totalVotes : 0}</p>
                    {/* <div className="shadow"></div> */}
                </div>
                <div className="date card">
                    <i className="icon fas fa-calendar-alt"></i>
                    <p className="title">Joined</p>
                    <p className="value">{userInfo.dateJoined}</p>
                    {/* <div className="shadow"></div> */}
                </div>
            </div>
            <div className="divider"></div>
            <div className="social">
                <SocialLink link={userInfo.socialLinks.instagram} username={username} logo="fab fa-instagram" name="Instagram: "></SocialLink>
                <SocialLink link={userInfo.socialLinks.youtube} username={username} logo="fab fa-youtube" name="Youtube: "></SocialLink>
                <SocialLink link={userInfo.socialLinks.twitter} username={username} logo="fab fa-twitter" name="Twitter: "></SocialLink>
                <SocialLink link={userInfo.socialLinks.facebook} username={username} logo="fab fa-facebook" name="Facebook: "></SocialLink>
                <SocialLink link={userInfo.socialLinks.reddit} username={username} logo="fab fa-reddit" name="Reddit: "></SocialLink>
                <SocialLink link={userInfo.socialLinks.pinterest} username={username} logo="fab fa-pinterest" name="Pinterest: "></SocialLink>
            </div>
            <div className="divider"></div>
            <User.Consumer>{
                user => <><div className="profile-btn" onClick={async () => {
                    let result = await FollowUser(username)
                    if (result.status == 'failure')
                        return showNotification(result.message)

                    if (result.message == 'followed') {
                        let updatedInfo = userInfo
                        updatedInfo.following.push(username)
                        user.addFollowing(username)
                        setUserInfo({ ...updatedInfo })
                    } else {
                        let updatedInfo = userInfo
                        updatedInfo.following = updatedInfo.following.filter(i => i != username)
                        user.removeFollowing(username)
                        setUserInfo({ ...updatedInfo })
                    }
                }}>
                    <p>{userInfo.following && user.following && user.following.includes(username) ? 'UNFOLLOW' : 'FOLLOW'}</p>
                </div>
                    {
                        user.rank != "" && user.rank != "user" && user.username != username && userInfo.rank == "user" ? <div className="profile-btn" onClick={async () => {
                            let res = await PromoteToMod(username)
                            if (res.status == 'success')
                                setUserInfo(state => ({ ...state, rank: 'mod' }))
                        }}>
                            <p>PROMOTE TO MOD</p>
                        </div> : <></>
                    }
                    {
                        user.rank != "" && user.rank != "user" && user.username != username && userInfo.rank == "mod" ? <div className="profile-btn" onClick={async () => {
                            let res = await DemoteToUser(username)
                            if (res.status == 'success')
                                setUserInfo(state => ({ ...state, rank: 'user' }))
                        }}>
                            <p>DEMOTE TO USER</p>
                        </div> : <></>
                    }
                </>
            }
            </User.Consumer>
        </div>
    )
}

function SocialLink(props) {
    const [link, setLink] = useState(props.link)

    useEffect(() => {
        setLink(state => props.link)
    }, [props.link])

    return (
        <div className="social-tile">
            <i className={props.logo}></i>
            <p>{props.name}</p>
            <p className="link">{link}</p>
            <User.Consumer>
                {
                    user => props.username == user.username ?
                        <i className="edit fas fa-edit" onClick={() => {
                            setModalVisibility("singletext", true, {
                                title: "Please enter your user handle:",
                                buttonText: "SUBMIT",
                                callback: async function(text) {
                                    let result = await ChangeSocial(props.name.split(':')[0].toLowerCase(), text)
                                    if (result.status == 'success') {
                                        setLink(text)
                                    }
                                }
                            })
                        }
                        }></i> :
                        <></>
                }
            </User.Consumer>
        </div>
    )
}

function ProfilePost(props) {

    return (
        <div className="profile-post">
            <div className="main">
                <p className="title">{props.title}</p>
                {/* <div className="tags">
                    {
                        props.tags.map(tag => <p key={tag}>{tag}</p>)
                    }
                </div> */}
                <p className="body">{props.body}</p>
            </div>
            <div className="votes">{props.votes}</div>
        </div>
    )
}

export async function openUserProfile(visibility, username) {
    currentUser = username
    setLoadScreen(true)
    let postsPromise = GetPostsByUser(currentUser)
    let userPromise = GetUserInfo(username)

    Promise.allSettled([postsPromise, userPromise])
        .then(results => {
            let posts = results[0].value
            let user = results[1].value

            if (posts.status == 'failure' || user.status == 'failure')
                return console.log('Failed')

            changePosts(posts.data)
            changeUserInfo(user.data)
            changeVisibility(visibility)
            setLoadScreen(false)
        })


}