/* eslint-disable eqeqeq */
import React, { useEffect, useState, useRef } from 'react'
import Post from './Post'
import { GetPosts, FollowTag, QuickSearch, GetPostsByUser, PageSearch } from '../Communicator'
import { setModalVisibility } from './Modal' //createpost
import { isAuthenticated, isEmptyOrSpaces } from '../Utils'
import { Page, User } from '../Config'
import { showPostPage } from './PostPage'
import { addToBackButtonStack, showUserProfile } from './RightMenu'
import { showNotification } from './NoticationBar'
import { SetDivision } from '../App'

export function PostContainer(props) {

  const [posts, setPosts] = useState([])
  const [filter, setFilter] = useState('new')
  const [viewType, setViewType] = useState(0)

  useEffect(() => {
    async function fetchData() {
      if (props.page.tags[props.page.tags.length - 1].includes('_user_profile')) {
        let username = props.page.tags[props.page.tags.length - 1].split('_')[0]
        let posts = await GetPostsByUser(username)

        if (posts.status == 'failure') return showNotification('Could not get Posts')

        return setPosts(posts.data)
      }

      let tagsToGet = props.username != null && props.page.tags.length == 1 ?
        [...props.page.currentTags, ...props.user.followingSports, ...props.user.followingCompetitions, ...props.user.followingTeams] : props.page.currentTags
      let posts = await GetPosts(tagsToGet, filter)
      setPosts(posts.data.posts)
    }

    fetchData()
  }, [props.page.currentTags, filter])

  function followText(user) {
    function execute(array) {
      let isUsed = false

      for (let item of array) {
        if (item[0] == props.page.currentTags[0]) {
          isUsed = true
          break
        }
      }

      return <div id="follow-btn" onClick={() => { isUsed ? followClicked(user, false) : followClicked(user, true) }}>{isUsed ? <p>Unfollow</p> : <p>Follow this!</p>}</div>
    }

    if (props.page.currentTags[0].includes('_team_result'))
      return execute(user.followingTeams)
    else
      return execute(user.followingCompetitions)
  }

  async function followClicked(user, isAdded) {
    let name, type

    if (props.page.currentTags[0].includes('_team_result')) {
      name = props.page.currentTags[0].split('_')[0]
      type = 'team'
    }
    else if (props.page.currentTags[0].includes('_competition_result')) {
      name = props.page.currentTags[0].split('_')[0]
      type = 'competition'
    }
    else {
      return
    }

    let result = await FollowTag(props.page.currentTags, name, type, isAdded)

    if (result.status == 'success') {
      if (type == 'team') {
        if (isAdded)
          user.editTeams([props.page.currentTags[0], name], 'add')
        else
          user.editTeams([props.page.currentTags[0], name], 'remove')
      } else {
        if (isAdded)
          user.editCompetitions([props.page.currentTags[0], name], 'add')
        else
          user.editCompetitions([props.page.currentTags[0], name], 'remove')
      }
    }
  }

  return (
    <div id="main-holder">
      {
        <div id="post-container">
          <SearchBar type="mobile" page={props.page}></SearchBar>
          <CreatePostBar></CreatePostBar>
          <User.Consumer>
            {user =>
              isAuthenticated() && props.page.tags.length > 1 &&
                (props.page.currentTags[0].includes('_team_result') || props.page.currentTags[0].includes('_competition_result')) ?
                followText(user)
                : <></>
            }
          </User.Consumer>
          <FilterBar viewType={viewType} setViewType={setViewType} setFilter={setFilter}></FilterBar>
          <CreatePostButton></CreatePostButton>
          <Page.Consumer>
            {page =>
              posts.map(post => (
                <Post
                  setDivision={props.setDivision}
                  setPosts={setPosts}
                  page={page}
                  viewType={viewType}
                  post={post}
                  key={post._id}
                  id={post._id}
                  votes={post.votes}
                  username={post.username}
                  title={post.title}
                  body={post.body}
                  time={post.time}></Post>
              ))
            }
          </Page.Consumer>
        </div>
      }
    </div>
  )
}

function CreatePostBar() {
  if (!isAuthenticated())
    return null

  return (
    <div id="create-post-bar">
      <i className="fas fa-newspaper"></i>
      <input placeholder="Create post..." onClick={() => { setModalVisibility("createpost", true) }}></input>
    </div>
  )
}

export function SearchBar(props) {

  const [typingTimer, setTypingTimer] = useState(null)
  const [activated, setActivation] = useState(false)
  const [searchResults, setSearchResults] = useState({ teams: [], posts: [], competitions: [], fixtures: [], users: [] })
  const [term, setTerm] = useState('')

  function inputChanged(term) {

    if (typingTimer)
      clearInterval(typingTimer)

    setTypingTimer(setTimeout(async () => {
      if (isEmptyOrSpaces(term)) {
        setActivation(false)
        return setSearchResults({ teams: [], posts: [], competitions: [], fixtures: [], users: [] })
      }

      setTerm(term)

      let result = await QuickSearch(term)
      if (result.status == 'success') {
        setSearchResults(result.data)
        setActivation(true)
      }
    }, 1000))
  }

  if(props.type == 'mobile'){
    return (
      <div className="searchbar mobile" onChange={(e) => { inputChanged(e.target.value) }} onClick={() => { }}>
        <i className="fas fa-search"></i>
        <input placeholder="Search..."></input>
        <SearchPage term={term} setSearchResults={setSearchResults} pageProvider={props.page} setVisibility={setActivation} activated={activated} searchResults={searchResults}></SearchPage>
      </div>
    )
  }
  else{
    return (
      <div className="searchbar desktop" onChange={(e) => { inputChanged(e.target.value) }} onClick={() => { }}>
        <i className="fas fa-search"></i>
        <input placeholder="Search..."></input>
        <SearchPage term={term} setSearchResults={setSearchResults} pageProvider={props.page} setVisibility={setActivation} activated={activated} searchResults={searchResults}></SearchPage>
      </div>
    )
  }
}

function SearchPage(props) {
  const searchPage = useRef(null)

  function documentClick(e){
    if (searchPage.current && !searchPage.current.contains(e.target)) {
        props.setVisibility(false)
    }
}

useEffect(() => {
    document.addEventListener('mousedown', documentClick)
    return () => {
        document.removeEventListener('mousedown', documentClick)
    }
}, [])

  if (!props.activated)
    return null

  let searchResults = props.searchResults

  async function TeamSearched(team) {
    props.pageProvider.setTags(['home', 'team', `${team}_team_result`])
    addToBackButtonStack(2, props.pageProvider, 1)
    props.setVisibility(false)
  }

  async function FixtureSearched(fixture) {
    props.pageProvider.setTags(['home', 'competition', `${fixture.league.name}_competition_result`, `${fixture.fixture_id}_fixture_details`])
    addToBackButtonStack(3, props.pageProvider, 2, fixture)
    props.setVisibility(false)
  }

  async function CompetitionSearched(competition) {
    props.pageProvider.setTags(['home', 'competition', `${competition}_competition_result`])
    addToBackButtonStack(2, props.pageProvider, 1)
    props.setVisibility(false)
  }

  async function UserSearched(user) {
    showUserProfile(user.username, props.pageProvider)
    props.setVisibility(false)
  }

  async function PostSearched(post) {
    post.totalVotes = post.votes.reduce((prev, curr) => prev.votes + curr.votes, { votes: 0 })
    let timeVal = parseInt(post.time)
    let date = new Date(timeVal)
    post.date = date.toLocaleString()
    SetDivision('post')
    showPostPage(true, post)
    props.setVisibility(false)
  }

  async function SearchInPage(tag, term) {
    let result = await PageSearch(term, tag)
    if (result.status == 'failure')
      return showNotification("Could not find anything")

    props.setSearchResults({ teams: [], posts: result.data, competitions: [], fixtures: [], users: [] })
  }

  let isEmpty = true

  for(let item in searchResults){
    if(searchResults[item].length > 0)
      isEmpty = false
  }

  return (
    <div id="search-page" ref={searchPage}>
      <div className="header">
        {
          props.pageProvider.tags.length > 2 ? <p className="extra-search" onClick={() => { SearchInPage(props.pageProvider.currentTags[props.pageProvider.currentTags.length - 1], props.term) }}>Search on this page only</p> : <></>
        }
      </div>
      {
        isEmpty ? <p>No items found.</p> : <></>
      }
      {searchResults.teams.length == 0 ? <></> :
        <div className="team-search section">
          <p className="title">Teams</p>
          <div className="divider"></div>
          {
            [...new Set(searchResults.teams.map(item => item.name))].map(item => <p onClick={() => { TeamSearched(item) }} className="result" key={item}>{item}</p>)
          }
        </div>
      }
      {
        searchResults.fixtures.length == 0 ? <></> :
          <div className="fixture-search section">
            <p className="title">Fixtures</p>
            <div className="divider"></div>
            {
              [...new Set(searchResults.fixtures)].map(item => <p onClick={() => { FixtureSearched(item) }} className="result" key={item.fixture_id}>{`${item.homeTeam.team_name} v ${item.awayTeam.team_name}`}</p>)
            }
          </div>
      }
      {
        searchResults.competitions.length == 0 ? <></> :
          <div className="competition-search section">
            <p className="title">Competitions</p>
            <div className="divider"></div>
            {
              [...new Set(searchResults.competitions.map(item => item.name))].map(item => <p onClick={() => { CompetitionSearched(item) }} className="result" key={item}>{item}</p>)
            }
          </div>
      }
      {
        searchResults.users.length == 0 ? <></> :
          <div className="user-search section">
            <p className="title">Users</p>
            <div className="divider"></div>
            {
              [...new Set(searchResults.users)].map(item => <p onClick={() => { UserSearched(item) }} className="result" key={item.username}>{item.username}</p>)
            }
          </div>
      }
      {
        searchResults.posts.length == 0 ? <></> :
          <div className="post-search section">
            <p className="title">Posts</p>
            <div className="divider"></div>
            {
              [...new Set(searchResults.posts)].map(item => <p onClick={() => { PostSearched(item) }} className="result" key={item.title}>{item.title}</p>)
            }
          </div>
      }
    </div>
  )
}

function FilterBar(props) {

  const [dropdownVisibility, setDropdownVisibility] = useState(false)
  const [buttonDisplayData, setButtonDisplayData] = useState(['NEW', 'fas fa-certificate'])
  const dropdown = useRef(null)

  function dropdownSelect(filter, icon) {
    props.setFilter(filter)
    setButtonDisplayData([filter.toUpperCase(), icon])
    setDropdownVisibility(false)
  }

  function documentClick(e) {
    if (dropdown.current && !dropdown.current.contains(e.target) && dropdownVisibility) {
      setDropdownVisibility(false)
    }
  }

  useEffect(() => {
    document.addEventListener('mousedown', documentClick)

    return () => {
      document.removeEventListener('mousedown', documentClick)
    }
  }, [dropdownVisibility])

  return (
    <div id="filterbar">
      <div id="filter-selector" ref={dropdown} onClick={() => { setDropdownVisibility(state => !state) }}>
        <i className={buttonDisplayData[1]}></i>
        <div className="divider"></div>
        <p>{buttonDisplayData[0]}</p>
        {
          !dropdownVisibility ? <></> : <div className="dropdown" onClick={(e) => { e.stopPropagation() }}>
            <p onClick={() => { dropdownSelect('Top', 'fas fa-chart-line') }}>Top</p>
            <p onClick={() => { dropdownSelect('Hot', 'fas fa-burn') }}>Hot</p>
            <p onClick={() => { dropdownSelect('Trending', 'fab fa-red-river') }}>Trending</p>
            <p onClick={() => { dropdownSelect('New', 'fas fa-certificate') }}>New</p>
            <p onClick={() => { dropdownSelect('Controvertial', 'fas fa-skull-crossbones') }}>Controvertial</p>
          </div>
        }
      </div>

      <div id="view-selector">
        <p>VIEW</p>
        <div className="divider"></div>
        <i onClick={() => { props.setViewType(0) }} className="fas fa-th-list"></i>
        <i onClick={() => { props.setViewType(1) }} className="fas fa-stream"></i>
      </div>
    </div>
  )
}

function CreatePostButton() {

  if (!isAuthenticated())
    return null

  return (
    <div id="create-post-btn" onClick={() => { setModalVisibility("createpost", true) }}>
      <div className="row">
        <i className="icon fas fa-envelope"></i>
        <p>CREATE POST</p>
      </div>
      <div className="shadow">
      </div>
    </div>
  )
}