import React, { useState, useRef, useEffect } from 'react'
import FitText from 'react-fittext'

export function Dropdown(props) {
    const [contentClass, setContentClass] = useState('content')
    const [title, setTitle] = useState(props.title)
    const dropdown = useRef(null)

    function documentClick(e){
        if (dropdown.current && !dropdown.current.contains(e.target)) {
            setContentClass('content')
        }
    }

    useEffect(() => {
        document.addEventListener('mousedown', documentClick)
        return () => {
            document.removeEventListener('mousedown', documentClick)
        }
    }, [contentClass])

    return (
        <div onClick={e => e.stopPropagation()} className="dropdown" style={{width: props.expanded != null ? props.expanded : '100px',zIndex: props.zIndex != null ? props.zIndex : {}}} ref={dropdown}>
            <div className="button">
                <div className="items" onClick={() => { contentClass == 'content' ? setContentClass('content open') : setContentClass('content') }}>
                    <p className="text">{title}</p>
                    <i className="trailing fas fa-caret-down"></i>
                </div>
                <div className="shadow"></div>
                <div className={contentClass}>
                    {
                        props.choices.map(choice => <p onClick={() => {choice.action(setTitle); setContentClass('content')}} key={choice.name}>{choice.name}</p>)
                    }
                </div>
            </div>

        </div>
    )
}