/* eslint-disable eqeqeq */
import React, { useEffect, useState, useContext } from 'react'
import { GetTeams, GetLeagues, GetFixturesInLeague, GetTeamMatches, GetFixturesInLeagueByName, GetPostsByUser } from '../Communicator'
import { showNotification } from './NoticationBar'
import { ShowContentPage } from './ContentPage'
import ContentPage from '../components/ContentPage'
import { Page } from '../Config'
import { setLoadScreen } from './LoadDialog'
import { ProfileDesktop } from './Profile'
import '../../node_modules/flagpack/dist/flagpack.css'

let updateFixtures
let updateTab
let changeContentPage
let currentMatch

let profileUsername = ''

let backButtonListenerStack = []

export function addToBackButtonStack(count, page, level, fixture = null) {
    backButtonListenerStack.length = 0
    if(fixture != null)
        currentMatch = fixture

    if (level == 1) {
        changeContentPage(false)

        for (let i = 0; i < count; i++) {
            // eslint-disable-next-line no-loop-func
            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]
                page.popPage(lastTab)
                updateTab(page.tags[page.tags.length - 1])
            })
        }
    }
    else if (level == 2) {
        ShowContentPage(true, fixture)
        changeContentPage(true)

        for (let i = 0; i < count; i++) {
            // eslint-disable-next-line no-loop-func
            backButtonListenerStack.push(() => {
                changeContentPage(false)
                let lastTab = page.tags[page.tags.length - 1]
                page.popPage(lastTab)
                updateTab(lastTab)
            })
        }
    }

}

export function showUserProfile(username, page) {
    profileUsername = username

    let lastTab = page.tags[page.tags.length - 1]

    updateTab(`${username}_user_profile`)
    page.setPage([`${username}_user_profile`])

    backButtonListenerStack.push(() => {
        page.popPage(lastTab)
        updateTab(page.tags[page.tags.length - 1])
    })
}

function RightMenu(props) {

    const [teams, setTeams] = useState([])
    const [leagues, setLeagues] = useState([])
    const [tab, setTab] = useState('home') //Redacted - Used to rerender when popPage is run as popPage doesnt initiate rerender for some reason
    const [fixtureTab, selectFixtureTab] = useState(1)
    const [fixtures, setFixtures] = useState([])
    const [isContentPageVisible, setContentPageVisibility] = useState(false)

    useEffect(() => {
        async function getData() {
            let res_team = await GetTeams()
            let res_league = await GetLeagues()

            if (res_team.status === "failure" || res_league.status === "failure")
                return console.log("Error occured while retrieving leagues/teams")

            setTeams(res_team.data.teams)
            setLeagues(res_league.data.leagues)
        }

        updateFixtures = setFixtures
        updateTab = setTab
        changeContentPage = setContentPageVisibility
        getData()
    }, [])

    // if (!props.activated)
    //     return null

    function fixtureTabClicked(tabNumber, team, previousTab, page) {
        selectFixtureTab(tabNumber)

        if (tabNumber == 1) {
            if (page.tags[page.tags.length - 1].includes('_selected_tab')) {
                page.popPage(page.tags[page.tags.length - 1])
            }

            page.setPage([team])
            updateTab(team)
        } else {

            if (page.tags[page.tags.length - 1].includes('_selected_tab')) {
                page.popPage(page.tags[page.tags.length - 1])
            }

            page.setPage([`${team}_selected_tab`])
            updateTab(`${team}_selected_tab`)
        }
    }

    return (
        <Page.Consumer>
            {page =>
                <div>
                    <div className="rightmenu" style={{ display: props.activated ? 'flex' : 'none' }}>
                        <RightMenuContent page={page}
                            setContentPageVisibility={setContentPageVisibility}
                            fixtureTabClicked={fixtureTabClicked}
                            tab={tab}
                            setTab={setTab}
                            fixtureTab={fixtureTab}
                            isContentPageVisible={isContentPageVisible}
                            fixtures={fixtures}
                            teams={teams}
                            leagues={leagues} ></RightMenuContent>
                    </div>
                    <div className="rightmenu desktop">
                        <RightMenuContent page={page}
                            setContentPageVisibility={setContentPageVisibility}
                            fixtureTabClicked={fixtureTabClicked}
                            tab={tab}
                            setTab={setTab}
                            fixtureTab={fixtureTab}
                            selectFixtureTab={selectFixtureTab}
                            isContentPageVisible={isContentPageVisible}
                            fixtures={fixtures}
                            teams={teams}
                            leagues={leagues} ></RightMenuContent>
                    </div>
                </div>
            }
        </Page.Consumer>
    )
}

function RightMenuContent({ page, setContentPageVisibility, selectFixtureTab, fixtureTabClicked, tab, setTab, fixtureTab, isContentPageVisible, fixtures, teams, leagues }) {
    return (<>
        {page.currentTags[page.currentTags.length - 1] === 'home' ? <div></div> : <div id="back-btn" onClick={() => { backButtonListenerStack.pop()() }} className="button fixed">
            <p> BACK </p>
        </div>}
        <div id="navigation-container">
            <Tabs setContent={setContentPageVisibility} tab={tab} setTab={setTab} page={page}></Tabs>
            <div id="navigation-body">
                {page.currentTags[page.currentTags.length - 1].includes('_fixture_details') || page.currentTags[page.currentTags.length - 1].includes('_selected_tab') ? <div className="team-selector">
                    <p onClick={() => fixtureTabClicked(0, `${currentMatch.homeTeam.team_name}`, tab, page)} className={fixtureTab == 0 ? 'selected' : ''}>{`${currentMatch.homeTeam.team_name}`}</p>
                    <p onClick={() => fixtureTabClicked(1, `${currentMatch.fixture_id}_fixture_details`, tab, page)} className={fixtureTab == 1 ? 'selected' : ''}>Both</p>
                    <p onClick={() => fixtureTabClicked(2, `${currentMatch.awayTeam.team_name}`, tab, page)} className={fixtureTab == 2 ? 'selected' : ''}>{`${currentMatch.awayTeam.team_name}`}</p>
                </div> : <></>}
                <ContentPage isActivated={isContentPageVisible}></ContentPage>
                <NavigationBody selectFixtureTab={selectFixtureTab} setContent={setContentPageVisibility} page={page} fixtures={fixtures} tab={tab} teams={teams} leagues={leagues}></NavigationBody>
            </div>
        </div>
    </>)
}

function tabClick(tab, setTab, page, name) {
    if (tab === name)
        return

    let lastTab = page.tags[page.tags.length - 1]
    page.popPage(lastTab)
    setTab(name)
    page.setPage([name])
}

function Tabs(props) {
    let tab = props.page.currentTags[props.page.currentTags.length - 1]
    let setTab = props.setTab //Redacted
    let page = props.page
    let setContent = props.setContent

    if (tab === 'home') {
        return (<></>)
    }
    else if (tab === 'competition' || tab === 'team' || tab === 'fixture') {
        return (
            <div id="navigation-tab">
                <div onClick={() => { tabClick(tab, setTab, page, 'competition') }} className="tab"><p className={tab === 'competition' ? 'dark' : 'light'}>COMPETITIONS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, 'team') }} className="tab"><p className={tab === 'team' ? 'dark' : 'light'}>TEAMS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, 'fixture') }} className="tab"><p className={tab === 'fixture' ? 'dark' : 'light'}>IN-PLAY</p></div>
            </div>
        )
    }
    else if (tab.includes('_competition_result') || tab.includes('_competition_fixture') || tab.includes('_competition_inplay') || tab.includes('_competition_team')) {
        let currentPage = tab.split('_')[0]

        return (
            <div id="navigation-tab">
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_team`); setContent(false) }} className="tab"><p className={tab.includes('_competition_team') ? 'dark' : 'light'}>TEAMS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_inplay`); setContent(false) }} className="tab"><p className={tab.includes('_competition_inplay') ? 'dark' : 'light'}>IN-PLAY</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_result`); setContent(false) }} className="tab"><p className={tab.includes('_competition_result') ? 'dark' : 'light'}>RESULTS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_competition_fixture`); setContent(false) }} className="tab"><p className={tab.includes('_competition_fixture') ? 'dark' : 'light'}>FIXTURES</p></div>
            </div>
        )
    }
    else if (tab.includes('_team_result') || tab.includes('_team_fixture') || tab.includes('_team_inplay')) {
        let currentPage = tab.split('_')[0]

        return (
            <div id="navigation-tab">
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_team_result`); setContent(false) }} className="tab"><p className={tab.includes('_team_result') ? 'dark' : 'light'}>RESULTS</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_team_fixture`); setContent(false) }} className="tab"><p className={tab.includes('_team_fixture') ? 'dark' : 'light'}>FIXTURES</p></div>
                <div onClick={() => { tabClick(tab, setTab, page, `${currentPage}_team_inplay`); setContent(false) }} className="tab"><p className={tab.includes('_team_inplay') ? 'dark' : 'light'}>IN-PLAY</p></div>
            </div>
        )
    }
    else {
        return null
    }
}

function NavigationBody(props) {

    let setContent = props.setContent
    let page = props.page
    let tab = page.currentTags[page.currentTags.length - 1]//props.tab
    let leagues = props.leagues
    let teams = props.teams
    let fixtures = props.fixtures
    let selectFixtureTab = props.selectFixtureTab

    const [teamFixtures, setTeamFixtures] = useState([])

    useEffect(() => {
        //Get results when searched
        (async () => {
            let lastTag = page.currentTags[page.currentTags.length - 1]
            if (!lastTag) return

            if (lastTag.includes('_team_result')) {
                setLoadScreen(true)
                let result = await GetTeamMatches(lastTag.split('_')[0])
                setTeamFixtures(result.data)
                setLoadScreen(false)
            } else if (lastTag.includes('_competition_result')) {
                setLoadScreen(true)
                let result = await GetFixturesInLeagueByName(lastTag.split('_')[0])
                updateFixtures(result.data)
                setLoadScreen(false)
            }
            else if (lastTag.includes('_fixture_details')) {
                let secondLastTab = page.tags[page.tags.length - 2]
                if (!secondLastTab) return

                setLoadScreen(true)
                let result = await GetFixturesInLeagueByName(secondLastTab.split('_')[0]) //Getting second-last tag
                updateFixtures(result.data)
                setLoadScreen(false)
            }
        })()

    }, [page.searchTerm])

    //console.log(page.tags)

    function competitionFixtureResolver(statusArray, icon) {
        let click = async (fixture) => {
            backButtonListenerStack.push(() => {
                selectFixtureTab(1)
                page.popPage(`${fixture.fixture_id}_fixture_details`)
                setContent(false)
                updateTab(tab)
            })

            setContent(true)
            currentMatch = fixture
            page.setPage([`${fixture.fixture_id}_fixture_details`]) //fixture.homeTeam.team_name, fixture.awayTeam.team_name
            updateTab(`${fixture.fixture_id}_fixture_details`)
            ShowContentPage(true, fixture)
        }

        let filtered = fixtures.filter((fixture) => statusArray.includes(fixture.statusShort))

        if(filtered.length == 0)
            return <InfoTile clicked={() => {}} name="NO RESULTS FOUND"></InfoTile>

        return (filtered.map(fixture => (
            <Tile icon={icon} clicked={() => { click(fixture) }} key={fixture.fixture_id} name={`${fixture.homeTeam.team_name} VS ${fixture.awayTeam.team_name}`} id={fixture.fixture_id}></Tile>
        ))
        )
    }

    function teamFixtureResolver(statusArray, icon) {
        let click = async (fixture) => {

            backButtonListenerStack.push(() => {
                selectFixtureTab(1)
                page.popPage(`${fixture.fixture_id}_fixture_details`)
                setContent(false)
                updateTab(tab)
            })

            setContent(true)
            currentMatch = fixture
            console.log(currentMatch)
            page.setPage([`${fixture.fixture_id}_fixture_details`]) //fixture.homeTeam.team_name, fixture.awayTeam.team_name
            updateTab(`${fixture.fixture_id}_fixture_details`)
            ShowContentPage(true, fixture)
        }

        let filtered = teamFixtures.filter((fixture) => statusArray.includes(fixture.statusShort))

        if(filtered.length == 0)
            return <InfoTile clicked={() => {}} name="NO RESULTS FOUND"></InfoTile>

        return (filtered.map(fixture => (
            <Tile icon={icon} clicked={() => { click(fixture) }} key={fixture.fixture_id} name={`${fixture.homeTeam.team_name} VS ${fixture.awayTeam.team_name}`} id={fixture.fixture_id}></Tile>
        ))
        )
    }

    if (tab === 'home') { //Sports Selection

        function click() {
            let lastTab = page.tags[page.tags.length - 1]

            updateTab('competition')
            page.setPage(['competition'])

            backButtonListenerStack.push(() => {
                page.popPage(lastTab)
                updateTab('home')
            })
        }

        return (
            <Tile name="SOCCER" clicked={() => { click() }}></Tile>
        )
    }
    else if (tab === 'competition') { //Competition

        let click = async (league) => {
            setLoadScreen(true)
            let result = await GetFixturesInLeague(league.leagueId)
            setLoadScreen(false)

            if (result.status === "failure") {
                showNotification(result.message)
                return
            }

            updateFixtures(result.data)
            updateTab(`${league.name}_competition_result`)

            page.setPage([`${league.name}_competition_result`])

            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]

                page.popPage(lastTab)
                updateTab('competition')
            })
        }

        console.log(leagues)

        return (
            leagues.map(league => (
                <Tile icon={league.countryCode != null ? `fp ${league.countryCode.toLowerCase()}` : null} key={league._id} name={league.name} clicked={() => { click(league) }}></Tile>
            ))
        )
    }
    else if (tab === 'team') { //Teams

        async function click(team) {
            setLoadScreen(true)
            let result = await GetTeamMatches(team.name)
            setTeamFixtures(result.data)
            setLoadScreen(false)

            if (result.status === "failure") {
                showNotification(result.message)
                return
            }

            page.setPage([`${team.name}_team_result`])
            updateTab(`${team.name}_team_result`)

            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]

                page.popPage(lastTab)
                updateTab('team')
            })
        }

        return (
            teams.map(team => (
                <Tile clicked={() => { click(team) }} key={team._id} name={team.name}> id={team.teamId}</Tile>
            ))
        )
    }
    else if (tab.includes('_competition_result')) { //In-League Matches
        return competitionFixtureResolver(["FT", "AET", "PEN"], 'fas fa-calendar-alt')
    }
    else if (tab.includes('_competition_fixture')) {
        return competitionFixtureResolver(["NS"], 'fas fa-calendar-alt')
    }
    else if (tab.includes('_competition_inplay')) {
        return competitionFixtureResolver(["1H", "HT", "2H", "ET", "P", "BT"], 'fas fa-stopwatch')
    }
    else if (tab.includes('_competition_team')) {
        let selectedCompetition = tab.split('_')[0]

        function click(team) {

            page.setPage([`${team.name}_team_result`])
            updateTab(`${team.name}_team_result`)

            backButtonListenerStack.push(() => {
                let lastTab = page.tags[page.tags.length - 1]

                page.popPage(lastTab)
                updateTab('team')
            })
        }

        return teams.filter(team => team.leagueName == selectedCompetition).map(team => (
            <Tile clicked={() => { click(team) }} key={team._id} name={team.name}> id={team.teamId}</Tile>
        ))

        // return (
        //     teams.map(team => (
        //         <Tile clicked={() => { click(team) }} key={team._id} name={team.name}> id={team.teamId}</Tile>
        //     ))
        // )
    }
    else if (tab.includes('_team_result')) {
        return teamFixtureResolver(["FT", "AET", "PEN"], 'fas fa-calendar-alt')
    }
    else if (tab.includes('_team_fixture')) {
        return teamFixtureResolver(["NS"], 'fas fa-calendar-alt')
    }
    else if (tab.includes('_team_inplay')) {
        return teamFixtureResolver(["1H", "HT", "2H", "ET", "P", "BT"], 'fas fa-stopwatch')
    }
    else if (tab.includes('user_profile')) {

        return <ProfileDesktop username={profileUsername}></ProfileDesktop>
    }
    else {
        return null
    }
}

function Tile(props) {
    return (
        <div className="team-tile" onClick={props.clicked}>
            {
                props.icon != null ? <i className={props.icon}></i> : <i className="logo fas fa-globe-europe"></i>
            }
            <div className="divider"></div>
            <p className="name">{props.name}</p>
        </div>
    )
}

function InfoTile(props){
    return (
        <div className="team-tile" onClick={props.clicked}>
            <p className="name">{props.name}</p>
        </div>
    )
}

export default RightMenu