/* eslint-disable eqeqeq */
import React, { createContext, Component, useContext, } from 'react'

export const User = createContext(null)
export const Page = createContext(null)

export class PageProvider extends Component {

    constructor(props) {
        super(props)

        this.setPage = (tag) => {
            this.setState(state => {

                for (let item of tag) {
                    if (!state.tags.includes(item)) {
                        state.tags.push(item)
                    }
                }

                state.currentTags = tag
                return state
            })
        }

        this.popPage = (tag) => {
            this.setState(state => {
                if (state.tags.includes(tag)) {
                    if (state.tags.length == 1)
                        return state

                    state.tags.pop()
                    state.currentTags = [state.tags[state.tags.length - 1]]

                    return state
                }
                return state
            })
        }

        this.setTags = (tags) => { //Only use for search as menu refresh relies on this
            this.setState(state => {
                state.tags = tags
                state.currentTags = [tags[tags.length - 1]]
                state.searchTerm = [tags[tags.length - 1]]
                return state
            })
        }

        this.state = {
            searchTerm: null,
            tags: ['home'],
            currentTags: ['home'],
            setPage: this.setPage,
            popPage: this.popPage,
            setTags: this.setTags
        }
    }

    render() {
        return (
            <Page.Provider value={this.state}>
                {this.props.children}
            </Page.Provider>
        )
    }
}

class UserProvider extends Component {
    constructor(props) {
        super(props)

        this.editUser = (details) => {
            this.setState(state => {
                state.username = details.username
                state.followingSports = details.followingSports == undefined ? [] : details.followingSport
                state.followingTeams = details.followingTeams == undefined ? [] : details.followingTeams
                state.followingCompetitions = details.followingCompetitions == undefined ? [] : details.followingCompetitions
                state.followers = details.followers == undefined ? [] : details.followers
                state.following = details.following == undefined ? [] : details.following
                state.rank = details.rank
                state.moderatedTags = details.moderatedTags
                return details
            })
        }

        this.editTeams = (team, type) => {
            this.setState(state => {
                let fTeams = [...state.followingTeams]

                if (type == 'add') {
                    for (let item of fTeams) {
                        if (item[0] == team[0])
                            return
                    }

                    fTeams.push(team)
                } else {
                    for (let i = 0; i < fTeams.length; i++) {
                        if (fTeams[i][0] == team[0])
                            fTeams.splice(i, 1)
                    }
                }

                return {...state, followingTeams: fTeams}
            })
        }

        this.editCompetitions = (competition, type) => {
            this.setState(state => {
                let fComps = [...state.followingCompetitions]

                if (type == 'add') {
                    for (let item of fComps) {
                        if (item[0] == competition[0])
                            return
                    }

                    fComps.push(competition)
                } else {
                    for (let i = 0; i < fComps.length; i++) {
                        if (fComps[i][0] == competition[0])
                            fComps.splice(i, 1)
                    }
                }
                return { ...state, followingCompetitions: fComps }
            })
        }

        this.addFollowing = (following) => {
            this.setState(state => {
                let set = new Set([...state.following, following])
                state.following = Array.from(set)
                return state
            })
        }

        this.removeFollowing = (following) => {
            this.setState(
                state => {
                    let set = new Set(state.following)
                    set.delete(following)
                    state.following = Array.from(set)
                }
            )
        }

        this.state = {
            editCompetitions: this.editCompetitions,
            editTeams: this.editTeams,
            editUser: this.editUser,
            addFollowing: this.addFollowing,
            removeFollowing: this.removeFollowing,
            isMod: () => this.state.rank != "" && this.state.rank != "user" ? true : false,
            followingSports: [],
            followingCompetitions: [],
            followingTeams: [],
            following: [],
            followers: [],
            username: null,
            rank: '',
            moderatedTags: [],
            token: null
        }
    }

    render() {
        return (
            <User.Provider value={this.state}>
                {this.props.children}
            </User.Provider>
        )
    }
}

export default UserProvider