import React, { useState, useContext, useEffect, useRef } from 'react'
import { setModalVisibility } from './Modal' //login, feedback
import { User, Page } from '../Config'
import { Dropdown } from './Dropdown'
import { RegisterPage, enablePage } from './RegisterPage'
import { logOut } from '../Utils'
import { openUserProfile } from './Profile'
import { showUserProfile, addToBackButtonStack } from './RightMenu'
import { SetDivision } from '../App'
import { loadLogs } from './ModeratorPage'

function LeftMenu(props) {

  // if (!props.activated)
  //   return null

  return (
    <div>
      <div className="leftmenu" style={{ display: props.activated ? 'flex' : 'none' }}>
        <CredentialBar nameClass={"credential-bar"}></CredentialBar>
        <MenuColumn></MenuColumn>
        <div id="menu-divider">
          <div className="line"></div>
          <p className="divider-text">FOLLOWING</p>
          <div className="line"></div>
        </div>
        <FollowingContainer></FollowingContainer>
      </div>
      <div className="leftmenu desktop">
        <CredentialBar nameClass={"credential-bar"}></CredentialBar>
        <MenuColumn></MenuColumn>
        <div id="menu-divider">
          <div className="line"></div>
          <p className="divider-text">FOLLOWING</p>
          <div className="line"></div>
        </div>
        <FollowingContainer></FollowingContainer>
      </div>
    </div>
  )
}

export function CredentialBar(props) { //Returns Register/login or profile/notification
  return (
    <User.Consumer>
      {user => {
        if (user.username == null) {
          return (<div className={`${props.nameClass}`}>
            <div className="button fixed" onClick={() => { enablePage() }}>
              <div className="items">
                <div className="leading">
                  <div className="icon dot"></div>
                  <p className="text">REGISTER</p>
                </div>
              </div>
              <div className="shadow"></div>
            </div>
            <div className="button fixed" onClick={() => { setModalVisibility("login", true) }}>
              <div className="items">
                <div className="leading">
                  <div className="icon dot"></div>
                  <p className="text">LOGIN</p>
                </div>
              </div>
              <div className="shadow"></div>
            </div>
          </div>)
        } else {
          return (
            <div className={`${props.nameClass}`}>
              <Page.Consumer>{
                page => <>
                  <Dropdown zIndex={21} choices={[
                    { name: 'Profile', action: () => { showUserProfile(user.username, page) } },
                    { name: 'Log Out', action: () => { logOut() } }]} title={user.username}></Dropdown>
                  {/* <i className="notification fas fa-bell"></i> */}
                </>
              }</Page.Consumer>
            </div>)
        }
      }}
    </User.Consumer>
  )
}

function MenuColumn() {
  return (
    <div id="menu-column">
      <MenuButton onPress={() => { window.location.reload() }} title='HOME' iconClass='fas fa-home'></MenuButton>
      <User.Consumer>
        {
          user => user.isMod() ? <MenuButton onPress={() => { SetDivision('modpage'); loadLogs() }} title='MODERATION' iconClass='fas fa-user-friends'></MenuButton> : <></>
        }
      </User.Consumer>
      <MenuButton onPress={() => { setModalVisibility("help", true)}} title='HELP' iconClass='fas fa-question-circle'></MenuButton>
      <MenuButton onPress={() => { setModalVisibility("feedback", true) }} title='FEEDBACK' iconClass='fas fa-comments'></MenuButton>
    </div>
  )
}

function FollowingContainer() {
  return (
    <div id="following-container">
      <User.Consumer>
        {
          user => {
            return (<>
              <FollowingButton title={"SPORTS"} items={user.followingSports.map(i => ['', i])}></FollowingButton> {/*Fix this*/}
              <FollowingButton title={"COMPETITIONS"} items={user.followingCompetitions}></FollowingButton>
              <FollowingButton title={"TEAMS"} items={user.followingTeams}></FollowingButton>
            </>)
          }
        }
      </User.Consumer>
    </div>
  )
}

function MenuButton(props) {
  return (
    <div className="button gap" onClick={() => { props.onPress() }}>
      <div className="items">
        <div className="leading">
          <i className={`icon ${props.iconClass}`}></i>
          <p className="text">{props.title}</p>
        </div>
        <div className="dot"></div>
      </div>
      <div className="shadow"></div>
    </div>
  )
}

function FollowingButton(props) {
  //props.title = teams/sports/competitions

  const [style, setStyle] = useState('')

  function itemClicked(item, page) {
    if (props.title == 'TEAMS') {
      TeamSearched(item, page)
    }
    else if (props.title == 'SPORTS') {

    }
    else if (props.title == 'COMPETITIONS') {
      CompetitionSearched(item, page)
    }
  }

  async function TeamSearched(team, page) {
    page.setTags(['home', 'team', `${team}_team_result`])
    addToBackButtonStack(2, page, 1)
  }

  async function CompetitionSearched(competition, page) {
    page.setTags(['home', 'competition', `${competition}_competition_result`])
    addToBackButtonStack(2, page, 1)
  }

  return (
    <div className="button gap" onClick={() => { style == '' ? setStyle('open') : setStyle('') }}>
      <div className="items">
        <div className="leading">
          <p className="text">{props.title}</p>
        </div>
        <i className="trailing fas fa-plus-circle"></i>
      </div>
      <Page.Consumer>
        {page => <div className={`follow-body ${style}`}>
          {
            props.items.map(item => <p className="follow-item" key={item} onClick={() => { itemClicked(item[1], page) }}>{item[1]}</p>)
          }
        </div>}
      </Page.Consumer>
      <div className="shadow"></div>
    </div>
  )
}

