import React, { useRef, useEffect, useState, useContext } from 'react'
import Embed from 'react-embed'
import { User, Page } from './Config'

export function isAuthenticated() {
  let cookie = document.cookie

  if (cookie === "")
    return false
  else
    return true
}

export function EmbedLink(link, viewType) {
  if (link.includes('fbcdn') || link.includes('twimg')) {
    return <img className="image" src={link} alt="[Content has been removed from host]" ></img>
  }
  else if (link.includes('youtube')) {
    return viewType == 0 ? <Embed url={link}></Embed> : <p className='video-placeholder'>VIDEO</p>
  }
}

export function getUser() {
  alert('not implemented')
}

export function getToken() {
  let cookie = document.cookie

  if (cookie === "")
    return null

  return cookie.split('=')[1]
}

export function logOut() {
  removeCookies()
  window.location.reload()
}

function removeCookies() {
  var res = document.cookie;
  var multiple = res.split(";");
  for (var i = 0; i < multiple.length; i++) {
    var key = multiple[i].split("=");
    document.cookie = key[0] + " =; expires = Thu, 01 Jan 1970 00:00:00 UTC";
  }
}

export function isEmptyOrSpaces(str) {
  return str === null || str.match(/^ *$/) !== null;
}

export function validURL(str) {
  try {
    new URL(str)
  } catch (_) {
    return false
  }
  return true
}

export function useModInBounds() {
  let userContext = useContext(User)
  let pageContext = useContext(Page)

  if (userContext.rank == 'user' || userContext.rank == '')
    return false

  if (userContext.rank == 'admin')
    return true

  if (userContext.moderatedTags.includes(pageContext.currentTags[pageContext.currentTags.length - 1])) {
    return true
  }
  else {
    return false
  }
}