import React, { useState, useEffect, useRef } from 'react'
import { DoVote, GetUserInfo, DeletePost, FlagPost, BanUser } from '../Communicator'
import { showPostPage } from './PostPage'
import { EmbedLink } from '../Utils'
import { User } from '../Config'
import { openUserProfile } from "./Profile"
import { showUserProfile } from './RightMenu'
import { setModalVisibility } from "./Modal"

export default function Post(props) {

  let timeVal = parseInt(props.time)
  let date = new Date(timeVal)

  const [votes, setVotes] = useState(0)

  useEffect(() => {

    let totalVotes = 0

    for (let i = props.votes.length - 1; i >= 0; i--) {
      totalVotes += props.votes[i].votes

      if (props.votes[i].tag === props.page.currentTags)
        break
    }

    setVotes(totalVotes)
  }, [props.page.currentTags])

  async function openProfile(username) {
    //loadscreen
    //let result = await GetUserInfo(username)

    openUserProfile(true, username)
  }

  return (
    <div className={props.viewType == 0 ? 'post' : 'post-compact'} onClick={() => { let newPost = props.post; newPost.totalVotes = votes; newPost.date = date.toLocaleString(); props.setDivision('post'); showPostPage(true, newPost) }}>
      <div className="header">
        <h1>{props.title}</h1>
        <User.Consumer>
          {
            user => {
              return <PostDropdown setPosts={props.setPosts} user={user} username={props.username} post={props.post}></PostDropdown>
            }
          }
        </User.Consumer>
      </div>
      <div className="compact-container">
        {
          props.post.isEmbed ? EmbedLink(props.post.embedUrl, props.viewType) : <p className="description">{props.body}</p>
        }
        <div className="divider"></div>
        <div className="row">
          <div className="column">
            <div className="user-details" onClick={(e) => { e.stopPropagation(); showUserProfile(props.username, props.page) }}>
              <p className="username">{props.username}</p>
              <div className="circle"></div>
              <i className="emblem fas fa-fire"></i>
            </div>
            <div className="timestamp">
              <p>{date.toLocaleString()}</p>
            </div>
          </div>
          <div className="voter">
            <div className="column">
              <i onClick={(e) => { e.stopPropagation(); likePost(props.id, props.page.currentTags, () => { setVotes(votes + 1) }) }} className="upvote fas fa-caret-up"></i>
              <p className="votes">{votes}</p>
              <i onClick={(e) => { e.stopPropagation(); dislikePost(props.id, props.page.currentTags, () => { setVotes(votes - 1) }) }} className="downvote fas fa-caret-down"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

function PostDropdown(props) {
  const dropdown = useRef(null)
  let user = props.user
  let post = props.post

  const [menuOpen, setMenuOpen] = useState(false)

  function isInBounds() {
    if (user.rank == 'user' || user.rank == '')
      return false

    if (user.rank == 'admin')
      return true

    if (user.moderatedTags.some(elem => post.tags.includes(elem))) {
      return true
    }
    else {
      return false
    }
  }

  function documentClick(e) {
    if (dropdown.current && !dropdown.current.contains(e.target)) {
      setMenuOpen(false)
    }
  }

  //const isModInBounds = useModInBounds()

  useEffect(() => {
    document.addEventListener('mousedown', documentClick)
    return () => {
      document.removeEventListener('mousedown', documentClick)
    }
  }, [])

  async function flagPost() {
    FlagPost(post._id)
  }

  async function muteUser() {
    setModalVisibility("muteuser", true, props.username)
  }

  async function banUser() {
    BanUser(props.username)
  }

  async function deletePost(id) {
    let result = await DeletePost(id)
    if (result.status == 'success') {
      props.setPosts(state => state.filter(p => p._id != id))
    }
  }

  return (
    <i className="fas fa-ellipsis-v" ref={dropdown} onClick={(e) => { e.stopPropagation(); setMenuOpen(state => !state) }}>
      {
        !menuOpen ? <></> : <div className="dropdown">
          {
            user.username == props.username || (user.isMod() && isInBounds()) ? <p onClick={(e) => {deletePost(props.post._id) }}>DELETE</p> : <></>
          }
          {
            user.username != props.username ? <p onClick={(e) => { flagPost() }}>FLAG</p> : <></>
          }
          {
            user.isMod() && isInBounds() && user.username != props.username? <p onClick={(e) => {muteUser() }}>MUTE</p> : <></>
          }
          {
            user.rank == 'admin' && user.username != props.username? <p onClick={(e) => { banUser() }}>BAN</p> : <></>
          }
        </div>
      }
    </i>
  )
}

function likePost(id, tag, callback) {
  DoVote('add', tag, id)
    .then((resp) => {
      if (resp.status === "success")
        callback()
    })
}

function dislikePost(id, tag, callback) {
  DoVote('subtract', tag, id)
    .then((resp) => {
      if (resp.status === "success")
        callback()
    })
}