import React, { useState, useEffect } from 'react'
import LeftMenu from './components/LeftMenu'
import { PostContainer, SearchBar } from './components/MainPage'
import RightMenu from './components/RightMenu'
import ContentPage from './components/ContentPage'
import { LoginModal, CreatePostModal, FeedbackModal, MuteUserModal, SingleTextModal, HelpModal } from './components/Modal'
import { NotificationBar } from './components/NoticationBar'
import { User, Page } from './Config'
import { isAuthenticated } from './Utils'
import { GetSelfInfo, Register } from './Communicator'
import { LoadScreen } from './components/LoadDialog'
import { Profile } from './components/Profile'
import { PostPage } from './components/PostPage'
import './style.css'
import { CredentialBar } from './components/LeftMenu'
import ModeratorPage from './components/ModeratorPage'
import {RegisterPage} from './components/RegisterPage'

let setDiv

function App(props) {

  const [menu, setMenu] = useState(0)
  const [division, setDivision] = useState('postlist') //Change to context

  setDiv = setDivision

  useEffect(() => {

    async function getUser() {
      if (isAuthenticated()) {
        let result = await GetSelfInfo()
        props.user.editUser({
          username: result.data.username,
          followingSports: result.data.followedSports,
          followingCompetitions: result.data.followedCompetitions,
          followingTeams: result.data.followedTeams,
          followers: result.data.followers,
          following: result.data.following,
          rank: result.data.rank,
          moderatedTags: result.data.moderatedTags
        })
      }
    }

    getUser()
  }, [])

  return (
    <>
      <User.Consumer>
        {user =>
          <div className="main-root">
            <NavBar changeMenu={setMenu} menu={menu}></NavBar>
            {/* <Profile></Profile> */} {/* REMOVED FULL SCREEN PROFILE TO IMMPLEMENT RIGHT BAR STYLE*/}
            <LoadScreen></LoadScreen>
            <LoginModal></LoginModal>
            <SingleTextModal></SingleTextModal>
            <HelpModal></HelpModal>
            <CreatePostModal></CreatePostModal>
            <RegisterPage></RegisterPage>
            <FeedbackModal></FeedbackModal>
            <MuteUserModal></MuteUserModal>
            <div id="desktop-container">
              <LeftMenu activated={menu === 1 ? true : false}></LeftMenu>
              <Division division={division} user={user} setDivision={setDivision}></Division>
              <RightMenu activated={menu === 2 ? true : false}></RightMenu>
            </div>
            <NotificationBar></NotificationBar>
          </div>
        }
      </User.Consumer>
    </>
  )
}

function Division(props) {

  if (props.division == 'postlist') {
    return <Page.Consumer>
      {page =>
        <PostContainer page={page} user={props.user} setDivision={props.setDivision}></PostContainer>
      }
    </Page.Consumer>
  }
  else if (props.division == 'post') {
    return <PostPage setDivision={props.setDivision}></PostPage>
  }
  else if (props.division == 'modpage') {
    return <ModeratorPage setDivision={props.setDivision}></ModeratorPage>
  }
}

function NavBar(props) {

  const onMenuClick = (val) => {

    if (props.menu === val) {
      props.changeMenu(0)
      return
    }

    props.changeMenu(val)
  }

  return (
    <div id="navbar">
      <i className="menu-selector fas fa-bars" onClick={() => { onMenuClick(1) }}></i>
      <img alt="logo" src="./media/logo.png" onClick={() => { window.location.reload() }} />
      <Page.Consumer>
        {
          page => <SearchBar type="desktop" page={page}></SearchBar>
        }
      </Page.Consumer>
      <i className="menu-selector fas fa-bars" onClick={() => { onMenuClick(2) }}></i>
      <CredentialBar nameClass={"credential-bar desktop"}></CredentialBar>
    </div>)
}

export function SetDivision(type) {
  setDiv(type)
}

export default App
