import React from 'react';
import ReactDOM from 'react-dom';
import './style.css';
import App from './App';
import UserProvider, { User, PageProvider } from './Config'

ReactDOM.render(
  <React.StrictMode>
    <UserProvider>
      <PageProvider>
        <User.Consumer>{
          user => <App user={user} />
        }
        </User.Consumer>
      </PageProvider>
    </UserProvider>
  </React.StrictMode>,
  document.getElementById('root')
);

